:root {
  --blue-gradient: rgb(106, 135, 228);
  --purpule-gradient: rgb(109, 59, 156);
  --white: rgb(255, 255, 255);
  --radius-small: 5px;
  --radius-medium: 10px;
  --red: red;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    90deg,
    rgb(106, 135, 228) 0%,
    rgb(109, 59, 156) 100%
  );
  background: linear-gradient(
    90deg,
    var(--blue-gradient) 0%,
    var(--purpule-gradient) 100%
  );
}

h1 {
  color: rgb(255, 255, 255);
  color: var(--white);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  margin: 10px;
  padding: 5px 10px;
  font-size: 20px;
  border-radius: 10px;
}

.form {
  font-size: 22px;
  background-color: var(--white);
  width: 50%;
  margin: auto;
  border-radius: var(--radius-small);
  padding: 20px;
}

.form__button {
  padding: 8px 12px;
  text-transform: uppercase;
  margin: 20px;
  font-size: 22px;
  border: 2px solid var(--purpule-gradient);
  background: var(--purpule-gradient);
  border-radius: var(--radius-medium);
  color: var(--white);
}

.active__header,
.done__header {
  width: 100%;
}

.active,
.done {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  margin: 20px auto;
  padding: 20px;
  border-radius: var(--radius-medium);
  background: var(--white);
}

.task {
  width: 100%;
}

.task__paragraph {
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 1rem;
  margin: 15px auto;
  background: var(--white);
  border: 2px solid var(--purpule-gradient);
  padding: 10px;
  border-radius: var(--radius-small);
}

.task__button {
  border: 2px solid var(--purpule-gradient);
  background: var(--purpule-gradient);
  color: var(--white);
  font-size: 0.9rem;
  letter-spacing: 1px;
  font-weight: 700;
  padding: 10px;
  border-radius: var(--radius-small);
  margin: 0 10px;
}

.task_button--group {
  margin-top: 10px;
  width: 100%;
}

.App {
  text-align: center;
  padding: 15px;
}

